<template>
  <div class="course">
    <navBar></navBar>
    <!-- hover出现的弹窗 保持左边筛选时hover弹窗位置不变-->
    <div class="hoverContent" ref="hoverContent" @mouseover="categoryHover('')" @mouseleave="categoryLeave">
      <div class="hoverContent-item" v-for="(iitem,iindex) of hoverContentItem.childrenList">
        <div class="secondTitle">{{iitem.title}}</div>
        <div class="thirdLevel">
          <span class="children-title" v-for="(iiitem,iiindex) of iitem.childrenList">{{iiitem.title}}</span>
        </div>
      </div>
    </div>

    <div class="course-content">
      <!-- 左边 -->
      <div class="course-content-left">
        <!-- 左上蓝色部分 -->
          <div class="header">
            <div>
              <span class="iconfont icon-category"></span>
              <span class="title">知学云</span>
            </div>
            <span class="iconfont icon-arrowBottomBold"></span>
            <ul>
              <li>全部课程</li>
              <li>知学云</li>
              <li>知学云学院</li>
              <li>思睦瑞科</li>
              <li>其他课程</li>
            </ul>
          </div>
          <!-- 左下 -->
          <div class="bottomPart">
              <div class="item" v-for="(item,index) of categroyList" :key="index" @mouseover="categoryHover(item)" @mouseleave="categoryLeave">
                <div class="item-title">{{item.title}}</div>
                <div class="item-children">
                  <span class="children-title" v-for="(iitem,iindex) of item.childrenList">{{iitem.title}}</span>
                </div>
              </div>
          </div>
      </div>
      <!-- 右边 -->
      <div class="course-content-right" ref="courseContentRight">
        <!-- 右边导航 -->
        <div class="right-header">
          <span>全部</span>>
          <span>疫苗临床部</span>>
          <span>新人入职考试</span>>
          <span>数学</span>
        </div>
        <!-- 右边筛选 类别 标签 -->
        <div class="choose">
            <div class="choose-item">
              <div :class="['cateList',cateListMoreBool ? 'cateListMore' : '']">
                <span>类别：</span>
                <span v-for="(item,index) of 20" :key="index">医学{{index + 1}}</span>
              </div>
              <div class="more" @click="cateMore">{{cateListMoreBool ? '更多' : '收起'}}<span :class="['iconfont',cateListMoreBool ? 'icon-arrowBottom' : 'icon-arrowUp']"></span></div>
            </div>
            <div class="choose-item">
              <div :class="['cateList',cateListMoreBool ? 'cateListMore' : '']">
                <span>标签：</span>
                <span v-for="(item,index) of 20" :key="index">医学{{index + 1}}</span>
              </div>
              <div class="more" @click="cateMore">{{cateListMoreBool ? '更多' : '收起'}}<span :class="['iconfont',cateListMoreBool ? 'icon-arrowBottom' : 'icon-arrowUp']"></span></div>
            </div>
        </div>
        <!-- 筛选条件 -->
        <div class="condition">
          <span>最新</span>
          <span>最热</span>
          <span>好评</span>
          <span>全部<b class="iconfont icon-arrowBottom"></b></span>
        </div>
        <!-- 筛选结果数据 -->
        <ul>
          <li v-for="(item,index) of 9" :key="index">
              <div class="img">
                <img src="https://zxy9.zhixueyun.com/default/M00/3C/10/Ci7mTV8XmVyAWZ3MAAE80Ep1ZvI341.jpg" />
              </div>
              <div class="bottomPart">
                <div class="title">测试标题</div>
                <div class="info">
                  <span>学分：1</span>
                  <span>学时：1</span>
                  <span>共：1节</span>
                </div>
                <div class="rate-browse">
                  <el-rate
                    v-model="rateValue"
                    disabled
                    text-color="#ff9900"
                    >
                  </el-rate>
                  <div class="browse">100人浏览过</div>
                </div>
              </div>
          </li>
        </ul>
        <!-- 分页 -->
        <div class="el-pagination">
          <el-pagination
            small
            background
            layout="prev, pager, next"
            :total="1000">
          </el-pagination>
        </div>
      </div>
    </div>
    <bottomPart></bottomPart>
  </div>
</template>

<script>
import { categoryList } from './data.js'
export default {
  name: "course",
  components:{

  },
  data() {
    return {
      cateListMoreBool:true,
      rateValue:3,
      categroyList:categoryList(),
      hoverContentItem:[]
    };
  },
  computed:{
    offsetLeft(){
      return this.$refs.courseContentRight.offsetLeft - 20
    }
  },
  mounted() {

  },
  methods: {
    //左边分类弹窗 hover
    categoryHover(item){
      //鼠标移到选中分类那一项 或者弹窗里都保持 hover状态
      if(item && item.id){
        this.hoverContentItem = item
      }
      this.$refs.hoverContent.style.display = 'block'
      this.$refs.hoverContent.style.left = this.offsetLeft + 'px'
    },
    //左边分类弹窗 leave
    categoryLeave(){
      this.$refs.hoverContent.style.display = 'none'
    },
    //更多（分类 标签）
    cateMore(){
      console.log(this.cateListMoreBool,'bool')
      this.cateListMoreBool = !this.cateListMoreBool
    }
  }
};
</script>
<style lang="scss" scoped>
  //左边分类hover弹窗
  .hoverContent{
    display: none;
    position: fixed;
    z-index: 10;
    top: 104px;
    width: 500px;
    height: 500px;
    background: #fff;
    box-shadow: 0px 0px 10px #ccc;
    padding: 20px;
    .hoverContent-item{
      margin-bottom: 20px;
    }
    .secondTitle{
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 20px;
    }
    .thirdLevel{
      span{
        margin-right: 20px;
        color: #666;
      }
    }
  }
    .course{
      background: #efefef;
      padding-top: 20px;
    .course-content{
      display: flex;
      width: 1200px;
      margin: 0 auto;
      padding-bottom: 100px;
      //左边
      .course-content-left{
        width: 220px;
        border-radius: 6px;
        // position: relative;
        // overflow: hidden;
        cursor: pointer;
        //左边 头部蓝色部分
        .header{
          display: flex;
          justify-content: space-between;
          height: 60px;
          align-items: center;
          padding: 0 20px;
          background:#0084CF;
          position: relative;
          border-top-left-radius: 6px;
          border-top-right-radius: 6px;
          ul{
            display: none;
            position: absolute;
            z-index: 10;
            top: 60px;
            left: 0;
            width: 100%;
            background: #fff;
            box-shadow: 0 10px 10px #ccc;
            li{
              border-bottom: 1px dashed  #dedede;
              padding: 16px 0;
              margin: 0 20px;
            }
          }
          &:hover ul{
            display: block;
          }
          div{
            display: flex;
            align-items: center;
          }
          .icon-category{
            color: #fff;
            font-size: 22px;
          }
          .title{
            color: #fff;
            margin-left: 20px;
          }
        }
        //左边 白色部分
        .bottomPart{
            background: #fff;
            .item{
              border-bottom: 1px dashed  #dedede;
              padding: 20px 20px 0;
              position: relative;
              .item-title{
                font-weight: bold;
                padding-bottom: 20px;
              }
              .item-children{
                color: #666;
                .children-title{
                  display: inline-block;
                  margin-right: 10px;
                  padding-bottom: 10px;
                }
              }
              &:hover{
                box-shadow: 0px 0px 10px #ccc;
              }
            }
        }

      }
      .course-content-right{
        flex: 1;
        margin-left: 20px;
        .right-header{
          color: #888;
          font-size: 12px;
          margin-bottom: 20px;
          span{
            padding: 0 10px;
            &:first-child{
              padding-left: 0;
            }
          }
        }
        .choose{
          margin: 20px 0;
          padding: 10px;
          border-radius: 10px;
          background: #fff;
          cursor: pointer;
          .choose-item{
            display: flex;
            border-bottom: 1px dashed #dedede;
            padding: 10px 0;
            &:last-of-type{
              border: none;

            }
            .cateList{
              color: #666;
              font-size: 12px;
              span{
                display: inline-block;
                margin: 0 20px 10px 0;
              }
            }
            .more{
              width: 60px;
              color: #0084CF;
            }
            .cateListMore{
              height: 16px;
              line-height: 16px;
              display: block;
              overflow: hidden;
              text-overflow: ellipsis;
              -ms-text-overflow: ellipsis;
            }
          }
        }
        .condition{
          display: flex;
          align-items: center;
          justify-content: flex-end;
          margin: 20px 0;
          span{
            padding: 0 10px;
            color: #666;
          }
        }
        ul{
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-gap:20px;
          grid-auto-rows:236px;
          li{
            height: 236px;
            border-radius: 10px;
            overflow: hidden;
            background: #fff;
            cursor: pointer;
            .img{
              width: 100%;
              height: 120px;
              overflow: hidden;
              img{
                width: 100%;
                height: 120px;
              }
            }
            &:hover img{
              transform: scale(1.08, 1.08);
              transition: all 0.5s ease-out;
            }
            .bottomPart{
              padding: 20px 10px;
              .title{
                font-weight: bold;
              }
              .info{
                margin: 12px 0;
                font-size: 12px;
                color: #999;
                span{
                  padding:0 10px;
                  &:first-child{
                    padding-left: 0;
                  }
                }
              }
              .rate-browse{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .rate{

                }
                .browse{
                  font-size: 8px;
                  color: #999;
                }
              }
            }
          }
        }
        .el-pagination{
          text-align: center;
          margin-top: 20px;
          >>> li{
            background: #fff;
          }
          >>> .active{
            color:#0084CF;
          }
          >>> button{
            background: #fff;
          }
        }
      }
    }
  }
</style>



